        <div class="block-legal">
          <h3 class="title title-36">POLTICA DE PROTEO DE DADOS</h3>
          <p>A direo/rgo diretivo da AUTO JUNTAS SAU (doravante, a pessoa responsvel pelo tratamento), assume a mxima responsabilidade e compromisso com o estabelecimento, implementao e manuteno desta poltica de proteco de dados, garantindo a melhoria contnua do controlador de dados. O objetivo de alcanar a excelncia no que respeita ao cumprimento do Regulamento (UE) 2016/679 do Parlamento Europeu e do Conselho, de 27 de abril de 2016, relativo  proteo das pessoas fisicas no que diz respeito ao tratamento de dados pessoais e  livre circulao destes dados e que revoga a Diretiva 95/46/CE (regulamento geral de proteo de dados) (OJEU L 119/1, 04-05-2016) e a legislao espanhola sobre a proteo de dados pessoais (lei orgnica, legislao setorial especfica e seus regulamentos de desenvolvimento).</p>
          <br>
          <p>A poltica de proteo de dados da AUTO JUNTAS SAU conta com o princpio da responsabilidade pr-ativa, segundo o qual o responsvel pelo controle  o cumprimento do quadro normativo e jurisprudencial que rege a referida poltica e  capaz de autoridades de controlo competentes.</p>
          <br>
          <p>Neste sentido, a pessoa responsvel pelo tratamento ser regida pelos seguintes princpios que devem servir tambm a todo o seu pessoal como guia e quadro de referncia no tratamento de dados pessoais:</p>
          <p>1. Proteo dos dados do projeto: o controlador ser aplicado, tanto no momento da determinao dos meios de tratamento e no momento do tratamento propriamente dito, medidas tcnicas e organizacionais apropriadas, como a pseudoutilizao, projetada para aplicar efetivamente o princpios de proteo de dados, tais como a minimizao de dados, e integrar as garantias necessrias no tratamento.</p>
          <p>2. Proteo de dados por padro: o controlador de dados aplicar as medidas tcnicas e organizacionais apropriadas para garantir que, por padro, somente os dados pessoais necessrios para cada um dos propsitos especficos do processamento sero processados.</p>
          <p>3. Proteo de dados no ciclo de vida da informao: as medidas que garantem a proteo dos dados pessoais sero aplicveis durante todo o ciclo de vida das informaes.</p>
          <p>4. Legalidade, lealdade e transparncia: os dados pessoais sero tratados de forma legal, justa e transparente em relao  parte interessada.</p>
          <p>5. Limitao por objetivo: os dados pessoais sero coletados para fins especficos, explcitos e legtimos, e no sero processados de forma incompatvel com os ditos propsitos.</p>
          <p>6. Minimizao dos dados: os dados pessoais sero adequados, pertinentes e limitados ao que  necessrio em relao aos fins para os quais so tratados.</p>
          <p>7. Exatido: os dados pessoais sero exatos e somente quando necessrio. Devem ser tomadas todas as medidas razoveis para que os dados pessoais imprecisos relativamente aos fins para os quais so tratados sejam corrigidos sem demora.</p>
          <p>8. Limitao do perodo de conservao: os dados pessoais sero mantidos de forma a permitir a identificao das partes interessadas durante no mais tempo do que o necessrio para efeitos de tratamento de dados pessoais.</p>
          <p>9. Integridade e confidencialidade: os dados pessoais sero tratados de forma a garantir a segurana adequada dos dados pessoais, incluindo a proteo contra o tratamento no autorizado ou ilegal e contra a perda, destruio ou danos acidentais, atravs da aplicao de medidas tcnicas ou organizacionais de segurana adequadas.</p>
          <p>10. Informao e formao: uma das chaves para garantir a proteo dos dados pessoais  a formao e informao prestada ao pessoal envolvido no seu tratamento. Durante o ciclo de vida da informao, todo o pessoal com acesso aos dados sero devidamente treinados e informado sobre suas obrigaes em relao ao cumprimento das regulamentaes de proteo de dados.</p>
          <br>
          <p>A poltica de proteo de dados da AUTO JUNTAS SAU  comunicada a todo o pessoal  responsvel pelo tratamento de dados e disponibilizada a todas as partes interessadas.</p>
          <br>
          <p>Consequentemente, esta poltica de proteo de dados envolve todo o pessoal responsvel pelo tratamento de dados, que deve conhec-la e assumi-la, considerando-a como sua, assim como cada membro responsvel pela sua aplicao, atentando as regras de proteo de dados aplicveis a sua atividade, bem como identificar e contribuir com as oportunidades de melhoria que julgar adequadas com o objetivo de alcanar a excelncia em relao a segurana de dados.</p>
          <br>
          <p>Esta poltica ser revista pela direo/rgo diretivo da AUTO JUNTAS SAU, quantas vezes a mesma julgar necessria, para adaptar, em todos os momentos, s atuais disposies relativas  proteo de dados pessoais.</p>
