      <p>Da mesma forma, pode estar ciente de suas informaes pessoais aquelas entidades que precisam ter acesso as mesmas para que possamos fornecer nossos servios. Por exemplo, nosso banco saber seus dados se o pagamento de nossos servios for feito por carto ou transferncia bancria quando e se houver esta opo de servio em sua regio.</p>
      <p>Da mesma forma, suas informaes sero conhecidas por entidades pblicas ou privadas s quais somos obrigados a fornecer seus dados pessoais devido ao cumprimento de qualquer lei. Para lhe dar um exemplo, a lei tributria exige que a Agncia tributria fornea certas informaes sobre operaes econmicas que excedam um determinado montante.</p>
      <p>No caso de, independentemente dos pressupostos acima mencionados, precisamos divulgar suas informaes pessoais para outras entidades, vamos pedir a sua permisso antecipadamente atravs de opes claras que lhe permitiro decidir a este respeito.</p>


      <p class="bold">Como vamos proteger os seus dados?</p>
