        <p>Voc tambm tem o direito de solicitar a transferncia de suas informaes para outra entidade. Este direito  chamado de "portabilidade" e pode ser til em determinadas situaes.</p>
        <p>Para solicitar qualquer um desses direitos, voc deve fazer um pedido por escrito para o nosso endereo, juntamente com uma fotocpia do seu documento de identidade oficial, para ser capaz de identific-lo.</p>
