historia : {
  texto_intro : "We have been a global reference for over 45 years. Working in search of constant improvement in both our products and our services.",
  hoy : "Today",
  texto_1972_1 : "AJUSA started up in 1972. That year saw the manufacture of the first cylinder head gasket. It took 3 hours and everything was done totally by hand!",
  texto_1972_2 : "We attended the first international trade fairs and extended our facilities by moving to the Campollano industrial estate.",
  texto_1980_1 : "We launched our first general catalogue with 600 references. The current catalogue has 20 times more! We have surpassed 100,000 references, providing complete coverage for Asian and European makes.",
  texto_1980_2 : "We managed to produce our first OEM-quality part: a cylinder head gasket for Renault.",
  texto_1990_1 : "We opened our Research and Testing Centre where we perform the most exhaustive quality assurance checks on our products.",
  texto_1990_2 : "Launch of 3 new product lines: cylinder head bolts, hydraulic lifters and camshafts.",
  texto_2000_1 : "Ajusa grows internationally and opens branches in Russia, United Kingdom, Mexico and Brazil, incorporating strategic partners around the globe.",
  texto_2000_2 : "We move into the business park that bears our name: Ajusa. With over 40,000 m2 in our production plant and 3,000 m2 for offices.",
  texto_2010_1 : "Since 2014, we produce oil feed pipes and we have launched our catalogue of turbo components with the most references in the market.",
  texto_2010_2 : "We apply the latest technology to ensure the maximum quality in our product lines.",
},
